<template>
  <div id="app">
    <nav id="nav" :class="{ light: isScroll }">
      <div class="container">
        <router-link class="logo" to="/">Akai Zhao 趙生凱</router-link>
        <div
          class="toggle"
          :class="{ show: isNavgationOpen }"
          @click="NavgationOn"
        ></div>
      </div>
    </nav>
    <div class="container">
      <router-view />
    </div>
    <NavList
      :isNavgationOpen="isNavgationOpen"
      @isNavgationOpenChanged="onIsNavgationOpenChanged"
    />
    <div class="cursor"></div>
  </div>
</template>
<script>
import NavList from "@/components/NavList.vue";
export default {
  data() {
    return {
      isNavgationOpen: false,
      isScroll: false
    };
  },
  components: {
    NavList
  },
  mounted() {
    document.addEventListener("scroll", this.navChangeHandler);
  },
  methods: {
    NavgationOn() {
      this.isNavgationOpen = !this.isNavgationOpen;
    },
    onIsNavgationOpenChanged(val) {
      this.isNavgationOpen = val;
    },
    navChangeHandler() {
      if (window.scrollY > 300) {
        this.isScroll = true;
      } else {
        this.isScroll = false;
      }
    }
  },
  watch: {
    $route() {
      this.isNavgationOpen = false;
    }
  }
};
</script>

