<template>
  <div class="portfoliio" :class="work.color">
    <div
      class="img_area"
      :style="{ backgroundImage: 'url(' + work.img + ')' }"
    ></div>
    <h2 class="name">{{ work.name }}</h2>
    <div class="intro_area">
      <p>簡介:</p>
      <p>{{ work.introText }}</p>
    </div>
    <a :href="work.webLink" target="_blank"></a>
  </div>
</template>
<script>
export default {
  props: {
    work: Object
  }
};
</script>
<style lang="scss" scoped>
.portfoliio {
  margin-bottom: 20vh;
  width: 90%;
  position: relative;
  @media (max-width: 996px) {
    margin: 0 auto;
    margin-bottom: 15vh;
    width: 80%;
  }
  cursor: pointer;
  .name {
    margin: 0;
    position: absolute;
    left: 5%;
    bottom: 10px;
    white-space: nowrap;
    cursor: initial;
    font-size: 90px;
    font-weight: bold;
    color: #ddd;
    z-index: 0;
    transition: 2s;
    @media (max-width: 996px) {
      font-size: 30px;
      left: 0;
      bottom: 0;
    }
  }
  &::after {
    position: absolute;
    left: 5%;
    bottom: 10px;
    white-space: nowrap;
    cursor: initial;
    font-size: 90px;
    font-weight: bold;
    color: #ddd;
    z-index: 0;
    transition: 2s;
  }
  .img_area {
    width: 100%;
    height: 40vh;
    background-repeat: no-repeat;
    filter: brightness(0.5);
    transition: 1s;
    @media (max-width: 996px) {
      height: 20vh;
    }
  }
  a {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    &::before,
    &::after {
      content: "";
      position: absolute;
      width: 60%;
      height: 30%;
      border: {
        style: solid;
      }
    }
    &::before {
      bottom: -20px;
      left: -20px;
      width: 80%;
      border: {
        width: 2px;
        color: #ccc;
        top: none;
        right: none;
      }
    }
    &::after {
      height: 40%;
      top: -20px;
      right: -20px;
      border: {
        left: none;
        bottom: none;
      }
    }
  }

  .intro_area {
    transform: translateY(40px);
    width: 35%;
    height: 100%;
    box-sizing: border-box;
    padding: 10px;
    opacity: 0;
    position: absolute;
    right: 10px;
    top: 0;
    p {
      font-size: 20px;
    }
  }
  &.red {
    a {
      &::after {
        border-color: #cc3635;
      }
    }
    .img_area {
      background-size: 150%;
      background-position: right center;
    }
  }
  &.brown {
    a {
      &::after {
        border-color: #da8536;
      }
    }
    .img_area {
      background-size: 100%;
      background-position: center;
    }
  }
  &.blue {
    a {
      &::after {
        border-color: #198cd1;
      }
    }
    .img_area {
      background-size: 120%;
      background-position: center;
    }
  }
  @media (min-width: 996px) {
    &:hover {
      .img_area {
        width: 60%;
      }
      .name {
        left: 100%;
        transform: translateX(-100%);
        color: transparent;
        -webkit-text-stroke-width: 3px;
      }
      .intro_area {
        opacity: 1;
        transition: 1.5s 0.45s;
        transform: translateY(0);
      }
      &.red {
        .img_area {
          background-size: 170%;
          filter: brightness(0.8);
        }
        .name {
          -webkit-text-stroke-color: #cc3635;
        }
      }
      &.brown {
        .img_area {
          background-size: 110%;
          filter: brightness(0.9);
        }
        .name {
          -webkit-text-stroke-color: #da8536;
        }
      }
      &.blue {
        .img_area {
          background-size: 130%;
          filter: brightness(0.9);
        }
        .name {
          -webkit-text-stroke-color: #198cd1;
        }
      }
    }
  }
}
</style>
